import React from 'react';
import './App.css';
import {HamburgerButton} from 'react-hamburger-button';
import {Layout, Menu, Icon} from 'antd';
import logo from './logo.svg'
var classNames = require('classnames');
const {Content} = Layout;
const SubMenu = Menu.SubMenu;

class App extends React.Component {
  state = {
    collapsed: true,
    open: false,
    openKeys: []
  };
  handleClick() {
    this.setState({
      open: !this.state.open,
      collapsed: !this.state.collapsed
    });
  }
  onOpenChange = (openKeys) => {
    const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
    this.setState({
      openKeys: latestOpenKey
        ? [latestOpenKey]
        : []
    });
  }

  render() {
    var menuClasses = classNames({'menu': true, 'menu-collapsed': this.state.collapsed});
    return (
      <div>
        <Layout
          className="barn-background"
          style={{
          minHeight: '100vh'
        }}>
          <Content style={{
            margin: '0 0px'
          }}>

            <div
              className='icon-square'
              style={{
              position: 'fixed'
            }}>
              <img src={logo} className="menu-logo"/>
            </div>
            <a
              className='hamburger-square'
              style={{
              position: 'fixed'
            }}
              onClick={this
              .handleClick
              .bind(this)}>
              <HamburgerButton
                open={this.state.open}
                onClick={this
                .handleClick
                .bind(this)}
                width={30}
                height={25}
                style={{
                margin: 'auto'
              }}
                strokeWidth={1}
                color='white'
                animationDuration={0.5}/>
            </a>

            <div
              className={menuClasses}
              style={{
              width: 240,
              position: 'fixed'
            }}>
              <Menu
                inlineCollapsed={this.state.collapsed}
                theme="light"
                openKeys={this.state.openKeys}
                onOpenChange={(this.onOpenChange)}
                defaultSelectedKeys={['1']}
                mode="inline">
                <Menu.Item key="1">
                  <Icon type="home"/>
                  <span>Home</span>
                </Menu.Item>
                <SubMenu
                  key="2"
                  title={<span><Icon type="info-circle"/><span>About</span></span>}>
                  <Menu.Item key="3">Legacy</Menu.Item>
                  <Menu.Item key="4">Team</Menu.Item>
                  <Menu.Item key="5">Culure</Menu.Item>
                </SubMenu>
                <Menu.Item key="leader">
                  <Icon type="star"/>
                  <span>Leadership</span>
                </Menu.Item>
                <SubMenu
                  key="sub1"
                  title={<span> <Icon type="area-chart"/> <span>Portfolio</span></span>}>
                  <Menu.Item key="3">Bernardsville</Menu.Item>
                  <Menu.Item key="4">Peapack-Gladstone</Menu.Item>
                  <Menu.Item key="5">Far Hills</Menu.Item>
                  <Menu.Item key="10">Mendham</Menu.Item>
                </SubMenu>
                <SubMenu
                  key="sub2"
                  title={<span><Icon type="bars"/><span>Strategy</span></span>}>
                  <Menu.Item key="6">Bearish</Menu.Item>
                  <Menu.Item key="8">Bullish</Menu.Item>
                </SubMenu>

                <Menu.Item key="9">
                  <Icon type="login"/>
                  <span>Investor Login</span>
                </Menu.Item>
                <Menu.Item key="13">
                  <Icon type="mail"/>
                  <span>Contact</span>
                </Menu.Item>
              </Menu>
            </div>

            <div className="hero-block">
              <h3 className="hero-intro">MELILLO EQUITIES IS A</h3>
              <h1 className="hero-title">real estate private equity with<br/>
                geographical concentration</h1>
            </div>
            <div className="hero-line">
              <h4 className="hero-learn">
                <a style={{
                  color: 'white'
                }}>LEARN ABOUT MELILLO EQUITIES
                  <Icon type="arrow-right"/></a>
              </h4>
            </div>
            <div className="hero-footer">
              THE COTTAGES AT VERNON MANOR
              <br/>
              <span className="title-name">The Vernon Manor Barn</span>
              <br/>
              <span style={{
                letterSpacing: .5 + 'px'
              }}>BOROUGH OF PEAPACK-GLADSTONE, NEW JERSEY</span>
            </div>
          </Content>
        </Layout>
        <div
          style={{
          height: 20 + 'vh',
          paddingTop: 10 + 'vh'
        }}>
          <h4 className="prop-list-title">MELILLO EQUITIES PORTFOLIO</h4>

        </div>
      </div>
    );
  }
}

export default App;



